import { FormErrors } from '../errors/validation.error';
import { ProductForm } from '../forms/product.forms';
import { createValidator, ValidationRules } from '../utils/validation.utils';

export const validateProductForm = (form: ProductForm): FormErrors<ProductForm> => {
  const errors: FormErrors<ProductForm> = {};

  errors.name = createValidator([
    { check: (v) => ValidationRules.required(v), message: 'Product name is required' },
    { check: (v) => ValidationRules.minLength(v, 2), message: 'Product name must be at least 2 characters' },
    { check: (v) => ValidationRules.maxLength(v, 100), message: 'Product name must be less than 100 characters' }
  ])(form.name);

  errors.productCode = createValidator([
    { check: (v) => !v || ValidationRules.required(v), message: 'Product code is required' },
    { check: (v) => !v || ValidationRules.minLength(v, 2), message: 'Product code must be at least 2 characters' },
    { check: (v) => !v || ValidationRules.maxLength(v, 50), message: 'Product code must be less than 50 characters' },
  ])(form.productCode);

  errors.description = createValidator([
    { check: (v) => !v || ValidationRules.minLength(v, 5), message: 'Description must be at least 5 characters' },
    { check: (v) => !v || ValidationRules.maxLength(v, 500), message: 'Description must be less than 500 characters' }
  ])(form.description);

  errors.category = createValidator([
    { check: (v) => ValidationRules.required(v), message: 'Category is required' },
    { check: (v) => ValidationRules.minLength(v, 2), message: 'Category must be at least 2 characters' },
    { check: (v) => ValidationRules.maxLength(v, 50), message: 'Category must be less than 50 characters' }
  ])(form.category);

  errors.quantity = createValidator([
    { check: (v) => v >= 0, message: 'Quantity cannot be negative' }
  ])(form.quantity);

  errors.unitPrice = createValidator([
    { check: (v) => v >= 0, message: 'Unit price cannot be negative' },
    { check: (v) => ValidationRules.decimal(v), message: 'Please enter a valid price' }
  ])(form.unitPrice);

  errors.entryDate = createValidator([
    { check: (v) => ValidationRules.required(v), message: 'Entry date is required' },
    { check: (v) => ValidationRules.date(v), message: 'Please enter a valid date' }
  ])(form.entryDate);

  errors.supplier = createValidator([
    { check: (v) => !v || ValidationRules.minLength(v, 2), message: 'Supplier name must be at least 2 characters' },
    { check: (v) => !v || ValidationRules.maxLength(v, 100), message: 'Supplier name must be less than 100 characters' }
  ])(form.supplier);

  errors.weight = createValidator([
    { check: (v) => v >= 0, message: 'Weight cannot be negative' },
    { check: (v) => ValidationRules.decimal(v), message: 'Please enter a valid weight' }
  ])(form.weight);

  errors.observations = createValidator([
    { check: (v) => !v || ValidationRules.maxLength(v, 1000), message: 'Observations must be less than 1000 characters' }
  ])(form.observations);

  errors.serialNumber = createValidator([
    { check: (v) => !v || ValidationRules.minLength(v, 2), message: 'Serial number must be at least 2 characters' },
    { check: (v) => !v || ValidationRules.maxLength(v, 50), message: 'Serial number must be less than 50 characters' }
  ])(form.serialNumber);

  errors.status = null; 

  return errors;
};
